@import "variabler";

body{
    display: grid;
    grid-template-columns: 300px 1fr;
    grid-template-rows: 60px 1fr; 
    font-size: $rem-multiplier;
    color: $dark-grey;
    font-family: $font-stack;

}
.logo{
    @include blue-gradient;
    display: flex;
    align-content: center;
    justify-content: center;
    border-bottom: 1px #e4e4e4 solid;

    p{
        color: white;
        font-size: 20px;
    }
}

.topbar{
    background: $white;
    border-bottom: 1px solid $grey;
}


.sidebar{
    @include blue-gradient;
    color: white;
    height: 100%;
    padding: 2rem;

    ul{
        list-style: none;
        padding: 0;
    }

    li{
        display: flex;
        align-items: center;
        margin: 1rem 0;
    }
    p{
        margin-left: 20px;
    }
}